.contact-container {
  /* width: 90%; */
  /* margin-left: 150px; */
  margin-top: 50px;
  display: flex;
  justify-content: center;
  background: linear-gradient(135deg, rgb(239, 240, 240), rgb(110, 185, 216)); /* Gradient background */
  background: linear-gradient(135deg, rgb(19, 88, 40), rgb(110, 185, 216)); 
  /* margin-left: 10px; */
  border-radius: 20px;
  margin-bottom: 10px;
  margin-left: 100px;
  width: 125vh;
}

.contact-card {
  display: flex;
  width: 100%;
  max-width: 1200px;
  /* background: #f8f9fa; */
  border-radius: 10px;
  overflow: hidden;
  /* box-shadow: 0px 0px 15px rgba(0,0,0,0.1); */
}

.contact-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.contact-image, .contact-form {
  flex: 1;
  padding: 20px;
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
}

.icon {
  margin: 0 10px;
  color: rgb(23, 30, 33);
  cursor: pointer;
  transition: transform 0.3s;
}

.icon:hover {
  transform: scale(1.2);
}

.divider {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.line {
  flex: 1;
  height: 1px;
  background: rgb(13, 16, 17);
}

.or {
  margin: 0 10px;
  font-weight: bold;
  color:rgb(16, 20, 21) ;
}

form input, form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 3px solid rgb(133, 207, 239);
  border-radius: 5px;
  /* background: black; */
}

form button {
  padding: 10px 20px;
  background: rgb(133, 207, 239);
  border: none;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
  transition: background 0.3s;
}

form button:hover {
  background: rgb(93, 173, 226);
}

input::placeholder {
  color: black; 
  font-weight: bold;
  opacity: 0.5;
}
textarea::placeholder {
  color: black; 
  font-weight: bold;
  opacity: 0.5;
  
}

.contact-title {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  color: black;
  margin-bottom: 20px;
}
