.navbar-profile-pic img{
    border-radius: 90px;
    height: 150px;
    width: 150px;
    display: flex;
    vertical-align: middle;
    margin: 0 auto;
    margin-top: 0px;
    
}

.nav-items{
    margin-top: 30px;
    /*text-align: center;*/
    margin-left: 20px;

}
.nav-link{
    margin-top: 20px;
    cursor:pointer;
    font-weight: bold;
    color: white;

}

.nav-link svg{
    margin-right: 10px;
    height: 25%;
    width: 25%;
}

.nav-link:hover{
    color:rgb(238, 255, 0);
    color: black;
    font-weight: bold;
}