.home-container {
    height: 300px;
    color: rgb(18, 175, 243);
    /* margin-left: 100px; */
    background: linear-gradient(135deg, rgb(239, 240, 240), rgb(110, 185, 216)); /* Gradient background */
    border-radius: 30px;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Soft shadow for a more modern look */
    padding: 20px; /* Added padding for better spacing */
    /* width: 150vh; */
    margin-left: 100px;
    width: 125vh;
}

.home-content h2 {
    padding: 0;
    padding-top: 80px;
    text-align: left;
    font-size: 28px; /* Increase font size for better readability */
    font-weight: bold; /* Make it stand out */
    color: rgb(18, 175, 243); /* Keep consistent color */
}

.home-content h1 {
    padding: 5px;
    font-size: 36px; /* Bigger text for the main heading */
    font-weight: bold;
    color: rgb(33, 36, 36); /* Light yellow to complement the design */
    text-shadow: 2px 2px rgba(0, 0, 0, 0.3); /* Added text shadow for depth */
}

.home-buttons {
    margin-left: 0;
    display: flex;
    justify-content: left; /* Centers buttons horizontally */
    gap: 10px; /* Adds space between buttons */
}

.home-buttons .btn-resume {
    background-color: rgb(18, 175, 243);
    color: white; /* Makes text white for contrast */
    width: 150px;
    padding: 10px;
    border-radius: 25px; /* Rounded corners for a modern look */
    border: none; /* Removes default button borders */
    text-align: center; /* Centers text inside button */
    font-size: 16px;
    font-weight: bold;
    transition: 0.3s ease-in-out; /* Smooth transition */
}

.home-buttons .btn-resume:hover {
    background-color: rgb(0, 162, 255); /* Slightly lighter blue on hover */
    width: 170px; /* Slightly increase button width */
    transform: translateY(-5px); /* Adds a slight lift effect */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15); /* Adds shadow on hover for depth */
}

