.articles {
    margin-left: 100px;
    margin-top: 20px;
    margin-bottom: 20px;
    /* width: 80%; */
    display: flex;
    flex-direction: column;
    background: linear-gradient(135deg, rgb(100, 8, 97), rgb(110, 185, 216)); 
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

    border-radius: 20px;
    /* align-items: center; */
    justify-content: center;
    width: 125vh;
}

.heading {
    margin-bottom: 50px;
    color: rgb(133, 207, 239);
    font-size: 2rem;
    font-weight: 600;
}

.row {
    margin: 30px 0px 50px 0px;
    display: flex;
    justify-content: space-between;
}

.card-notify-badge {
    position: absolute;
    left: -10px;
    top: -20px;
    width: 100px;
    height: 25px;
    background-color: rgb(133, 207, 239);
    border-radius: 20px;
    text-align: center;
    color: white;
    font-weight: bold;
    font-size: 0.9rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.picture img {
    height: 150px;
    width: 100%;
    /* padding-top: 20px; */
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    /* padding-left: 30px; */
    /* padding-right: 30px; */
    
    /* object-fit: cover;  */
}

.card-styler {
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card-styler:hover {
    transform: scale(1.05);
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.15);
}

.card-detail-badge {
    background-color: rgb(133, 207, 239);
    text-align: center;
    border-radius: 8px;
    padding: 10px;
    font-size: 1rem;
    color: white;
}

.card-image-overlay span {
    display: inline-block;
    margin-left: 5px;
    margin-top: 5px;
    font-weight: bold;
    color: white;
    border: none;
    font-size: 1rem;
}

#title {
    color: rgb(133, 207, 239);
    font-size: 1.8rem;
    font-weight: 700;
}
