.about{
    margin: 40px 0px;
    padding: 20px;
    margin-left: 0px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    border-radius: 20px;
    /* width: 150vh; */
    /* background-color: rgb(57, 56, 55); */
    background: linear-gradient(135deg, rgb(6, 50, 100), rgb(110, 185, 216)); /* Gradient background */
    margin-left: 100px;
    width: 125vh;
}
.about-img img{
    border-radius: 10px;
    height: 700px;
    width: 100%;
    /* background-color: black; */
}

.about-text{
    color:rgb(133, 207, 239);
    color:white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    font-size: 20px;
}
.about-text h1 {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.about-text h3 {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}