.project{
    margin: 50px 0px;
    margin-left: 0px;
    align-items: center;
    justify-content: center;
    margin-left: 100px;
    width: 125vh;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    background: linear-gradient(135deg, rgb(100, 8, 97), rgb(110, 185, 216)); 
   

    

}
.heading{
    margin-bottom: 50px;
    color: rgb(133, 207, 239);
    padding-top: 40px;
    /* background-color: rgb(48, 47, 45); */

}
.row{
    margin: 30px 0px 30px 0px;
    
}

.card-notify-badge{
    position:absolute;
    left:-10px;
    top:-20px;
    width: 100px;
    height: 25px;
    background-color: rgb(133, 207, 239);
    border-radius: 30px;
    text-align: center;
}
.card img{
    height: 150px;
    width: 100%;
    padding-top: 10px;
    /* padding-top: 20px; */
    padding-bottom: 10px;
    /* padding-left: 30px; */
    /* padding-right: 30px; */
}
.card-styler{
    background-color: rgb(0, 0, 0);
}
.card-detail-badge{
    background-color: rgb(133, 207, 239);
    text-align: left;
    border-radius: 30px;
    padding: 10px 10px;
    font: 10px;
}
.card-image-overlay span{
    display: inline-block;
    margin-left: 0px;
    
    color:black
}
#title{
    color: rgb(133, 207, 239);
}